/*-----------colours----------*/
/*-----------mixins----------*/
.button {
  border-radius: 4px;
  padding: 2em 5em 2em 5em;
  -webkit-box-shadow: 0px 3px 5px 0px #b0b8bd;
          box-shadow: 0px 3px 5px 0px #b0b8bd;
  margin: 1em;
}
/*----------- ----------*/
body {
  overflow: hidden;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  text-align: center;
}
a {
  text-decoration: none;
}
.App {
  height: 100vh;
  width: 100vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
/*--------------------NAVIGATION BUTTONS---------*/
.NavButton {
  color: #ffffff;
  background-color: #02b196;
  border-radius: 4px;
  padding: 2em 5em 2em 5em;
  -webkit-box-shadow: 0px 3px 5px 0px #b0b8bd;
          box-shadow: 0px 3px 5px 0px #b0b8bd;
  margin: 1em;
}
.NavButton:hover {
  background-color: #057a64;
}
.NavButtonGreyed {
  color: #f1f1f1;
  background-color: #bdb6b0;
  border-radius: 4px;
  padding: 2em 5em 2em 5em;
  -webkit-box-shadow: 0px 3px 5px 0px #b0b8bd;
          box-shadow: 0px 3px 5px 0px #b0b8bd;
  margin: 1em;
}
.PageContent {
  width: 100%;
  padding-top: 2em;
}
@-webkit-keyframes slide {
  from {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  to {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
  }
}
@keyframes slide {
  from {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  to {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
  }
}


